@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.MovieRow{

    margin-bottom: 30px;

}

.MovieRow h2{
    margin: 0px 0px 0px 30px;
}

.MovieRow--ListArea {

    overflow-x: hidden;
    padding-left: 30px;
}

.MovieRow--List {
    transition: all ease 0.75s;
}

.MovieRow--Left,
.MovieRow--Right{

    position: absolute;
    z-index: 99;

    display: flex;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 250px;

    margin-top: 30px;
    
    overflow: hidden;
    cursor: pointer;

    opacity: 0;

    transition: all ease 0.75s;
}

.MovieRow--Left{

    left: 0;
    background: linear-gradient(to right, #111 25%, transparent);
}

.MovieRow--Right{

    right: 0;
    background: linear-gradient(to left, #111 25%, transparent);
}

.MovieRow:hover .MovieRow--Left,
.MovieRow:hover .MovieRow--Right{

    opacity: 1;

}

@media (max-width: 760px) {

    .MovieRow--Left,
    .MovieRow--Right{

        opacity: 1;
    }
}
.MovieRow--Item {

    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.MovieRow--Item img {

    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.5s;
}

.MovieRow--Item img:hover {
    transform: scale(1.0);
}

.MovieRow--Item img:focus {
    transform: scale(1.0);
}



.Featured{
    height: 100vh;
}

.Featured--Vertical-Gradient{

    width: inherit;
    height: inherit;

    background: linear-gradient(to top,#111 10%, transparent 90%);
}

.Featured--Horizontal-Gradient{

    width: inherit;
    height: inherit;

    background: linear-gradient(to right,#111 20%, transparent );

    display: flex;
    flex-direction: column;
    justify-content: center;

    padding-left: 30px;
    padding-top: 70px;
    padding-bottom: 150px;
}

.Featured--Name{
    font-size: 60px;
    font-weight: bold;
}

.Featured--Info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
}

.Featured--Rated,
.Featured--Year,
.Featured--Seasons{

    display: inline-block;
    margin-right: 10px;
}

.Featured--Rated{
    color: #46d369;
}

.Featured--Description{

    margin-top: 15px;
    font-size: 20px;
    color: #999999;
    max-width: 50%;
}

.Featured--Buttons{
    margin-top: 15px;
}

.Featured--WatchButton,

.Featured--MyListButton {

    display: inline-block;
    padding: 12px 25px;
    margin-right: 25px;

    font-size: 20px;
    font-weight: bold;
    text-decoration: none;

    border-radius: 5px;
    
    opacity: 1;
    transition: all ease 0.2s;
}

.Featured--WatchButton:hover,

.Featured--MyListButton:hover {
    opacity: 0.7;
}

.Featured--WatchButton{

    color: black;
    background-color: white;
}

.Featured--MyListButton {

    color: white;
    background-color: #333;
}

.Featured--Genres{

    margin-top: 15px;
    font-size: 18px;
    color: #999
}

@media (max-width: 760px){

 .Featured--Name {
     font-size: 40px;
 }

  .Featured--Info {
     font-size: 18px;
 }

  .Featured--Description {
      
      font-size: 16px;

      max-width: 100%;
      margin-right: 30px
 }

.Featured--WatchButton,
.Featured--MyListButton {

    font-size: 15px;
}

.Featured--Genres{
    font-size: 14px
}

}
header{

    position: fixed;
    display: flex;

    justify-content: space-between;
    align-items: center;

    z-index: 999;

    top: 0;
    left: 0;
    right: 0;
    height: 70px;

    padding: 0 30px;
    background: transparent;

    transition: all ease 0.5s;
}

header.Black {
    background: linear-gradient(to bottom, #141414 40%, transparent 100%);
}

header.Gradient{

    background: linear-gradient(to bottom, #141414, transparent);
}

.Header--Logo{
    height: 25px;
} 

.Header--Logo img{

    height: 100%;
}

.Header--User{
    height: 45px;
} 

.Header--User img{

    height: 100%;
    border-radius: 2px;
}


.Loading, .Loaded{

    position: fixed;
    z-index: 99;

    background-color: black;

    left: 0;
    right: 0;
    top: 0;
    bottom: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.Loading img {

    width: 700px;
}

.Loaded img {

    width: 100%;
}

@media (max-width: 760px){

.Loaded img {

    height: 100%;
    width: 225%;
}
}
